import { Card, CardContent, Chip, Stack, Typography, Box, LinearProgress, alpha } from "@mui/material";
import WarningAmberIcon from "@mui/icons-material/WarningAmber";
import SecurityIcon from "@mui/icons-material/Security";

import { AlertEvent } from "../../types/security";
import { formatDateTime } from "../../utils/formatters";

type AlertCardProps = {
  alert: AlertEvent;
};

const severityColor: Record<AlertEvent["severity"], "error" | "warning" | "info"> = {
  critical: "error",
  high: "warning",
  medium: "warning",
  low: "info"
};

const severityLabel: Record<AlertEvent["severity"], string> = {
  critical: "Critique",
  high: "Élevée",
  medium: "Modérée",
  low: "Faible"
};

const AlertCard = ({ alert }: AlertCardProps) => (
  <Card
    variant="outlined"
    sx={(theme) => ({
      borderRadius: 20,
      borderColor: alpha(theme.palette.error.main, 0.16),
      background: alpha(theme.palette.background.paper, 0.9)
    })}
  >
    <CardContent>
      <Stack spacing={2}>
        <Stack direction="row" alignItems="center" justifyContent="space-between">
          <Chip
            icon={<WarningAmberIcon />}
            color={severityColor[alert.severity]}
            label={severityLabel[alert.severity]}
            size="small"
          />
          <Typography variant="caption" color="text.secondary">
            {formatDateTime(alert.detectedAt)}
          </Typography>
        </Stack>
        <Typography variant="subtitle1" fontWeight={600}>
          {alert.title}
        </Typography>
        <Typography variant="body2" color="text.secondary">
          {alert.description}
        </Typography>
        <Stack direction="row" spacing={1} flexWrap="wrap">
          {alert.tags.map((tag) => (
            <Chip
              key={tag}
              variant="outlined"
              size="small"
              label={tag}
              icon={<SecurityIcon fontSize="small" />}
            />
          ))}
        </Stack>
        <Box>
          <Typography variant="caption" color="text.secondary">
            Confiance
          </Typography>
          <LinearProgress
            variant="determinate"
            value={alert.confidence}
            sx={{ height: 6, borderRadius: 999, mt: 0.5 }}
            color="primary"
          />
        </Box>
      </Stack>
    </CardContent>
  </Card>
);

export default AlertCard;

